.home {
  text-align: center;
  .text01 {
    display: flex;
    justify-content: center;
    span {
      font-size:40px;
      color: var(--darkTxt);
    }
    p {
      font-size:45px;
      padding:20px;
      text-align:center;
      font-weight:500;
      line-height: 1.2;
      color: var(--darkTxt);
    }
  }
  .avatarImg {
    width: 200px;
    padding: 40px 0 80px 0;
  }
  .iconWrap {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    a {
      display: block;
      font-size: 40px;
      color: var(--darkiconClr);
      :hover {
        color:$pointClr;
      }
    }
  }
  .btnDown {
    margin:50px auto 0 auto;
    display:inline-block;
    padding:10px 25px;
    border:1px solid $pointClr;
    border-radius:8px;
    font-size:16px;
    font-weight: 600;
    color: $pointClr;
    background: linear-gradient(to left, #FFF 50%, $pointClr 50%) right;
    background-size: 200%;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    &:hover {
      background-position: left;
      color: #FFF;
      border-radius:25px;
    }
  }
}

/* Tablet */
@media only screen and (max-width: 1119px) {

}

/* Mobile */
@media only screen and (max-width: 800px) {
  .home {
    .text01 {
      span {
        font-size:8vw;
      }
      p {
        font-size:6vw;
        padding:5vw;
      }
    }
    .avatarImg {
      width: 50vw;
      padding: 10vw 0 20vw 0;
    }
    .iconWrap {
      gap: 10vw;
      a {
        font-size: 10vw;
      }
    }
    .btnDown {
      margin: 10vw auto 0 auto;
      padding: 3vw 7vw;
      border-radius: 3vw;
      font-size: 4vw;
      color: #FFF;
      background:$pointClr;
      &:hover {
        border-radius: 3vw;
      }
    }
  }
}