.work {
  .workWrap {
    width: 95%;
    max-width: 1000px;
    margin:auto;
    border-top: 1px solid #818181;

    .workList {
      box-sizing: border-box;
      .title {
        padding: 20px;
        font-size: 22px;
        font-weight: 600;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: var(--darkTxt);
        cursor: pointer;
        transition: all 0.2s ease-in-out;
        &:hover {
          background-color: $pointClr;
          color:#FFF;
        }
        .icoPlus {display: block;}
        .icoMinus {display: none;}
      }

      .workBox {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        height: 0;
        overflow: hidden;
        box-sizing: border-box;
        border-bottom: 1px solid #818181;
        .imgWrap {
          position: relative;
          width: 40%;
          margin: 20px;
          box-sizing: border-box;
          .linkWrap {
            display: none;
            justify-content: center;
            align-items: center;
            width: 96%;
            height: 65%;
            position: absolute;
            top: 6%;
            left: 4%;
            background-color: rgba(0,0,0,0.6);
            .btnView {
              display: block;
              width: 150px;
              height: 40px;
              line-height: 40px;
              border:1px solid #FFF;
              border-radius: 8px;
              text-align: center;
              font-weight: 600;
              color: #FFF;
              z-index: 4;
              background: linear-gradient(to left, transparent 50%, $pointClr 50%) right;
              background-size: 200%;
              transition: all 0.2s ease-in-out;
              &:hover {
                background-position: left;
                border: 1px solid $pointClr;
                border-radius: 20px;
              }
            }
          }
          
          &:hover .linkWrap {display: flex;}

        }
        .workText {
          padding: 20px;
          width: 60%;
          height: 100%;
          color: var(--darkTxt);
          strong {
            margin:20px 0 10px 0;
            font-size: 15px;
            font-weight: 600;
            color: var(--darkTxt);
            display: block;
          }
          ul {
            li {
              float:left;
              margin:0 5px 5px 0;
              display: flex;
              justify-content: center;
              align-items: center;
              padding: 0 15px;
              height: 30px;
              font-size: 13px;
              border-radius: 8px;
              background-color: var(--circlr);
              font-weight: 600;
            }
            &:after {
              display:block;
              content:"";
              clear: both;
            }
          }
          .gitlink {
            display: block;
            margin-top: 30px;
            font-size: 13px;
            a:hover {
              color: $pointClr;
            }
          }
        }
      }

      &.on {
        > .title {
          .icoPlus {display: none;}
          .icoMinus {display: block;}
        }
      }
      &.on > .workBox {
        height: auto;
        border-top: 1px solid #818181;
      }
    }
  }
}

/* Mobile */
@media only screen and (max-width: 800px) {
  .work {
    .workWrap {
      .workList {
        .workBox {
          display: block;
          .imgWrap {
            position: relative;
            width: 96%;
            margin: 4vw auto;
            .linkWrap {
              display: block;
              position: relative;
              width: 100%;
              height:auto;
              background: none;
              top: 0;
              left: 0;
              .btnView {
                display: block;
                width: 50%;
                height: 10vw;
                margin: 5vw auto 0 auto;
                line-height: 10vw;
                border:1px solid $pointClr;
                background-color: $pointClr;
                border-radius:2vw;
                font-size: 4vw;
                &:hover {
                  border-radius: 2vw;
                }
              }
            }
          }
          .workText {
            padding: 3vw;
            width: 100%;
            strong {
              font-size:4vw;
            }
            ul {
              margin:0 2vw 2vw 0;
              li {
                padding: 0 5vw;
                height: 6.5vw;
                font-size: 3vw;
                border-radius: 2vw;
                background-color: var(--circlr);
              }
            }
          }
        }
  
        &.on {
          .title p {
            .icoPlus {display: none;}
            .icoMinus {display: block;}
          }
        }
        &.on > .workBox {
          height: auto;
          border-top: 1px solid #BBB;
        }
      }
    }
  }
}