.header {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding:50px 0;

  .logo {
    position: absolute;
    top:center;
    left:0;
    right: 0;
    width: 50px;
    height: 50px;
    background: var(--logoImg);
    background-size: 100%;
  }

  .navBtn {
    display: none;
  }

  .menuWrap {
    display: flex;
    justify-content: center;
    align-items: center;
    .menu {
      display: flex;
      justify-content: center;
      align-items: center;
      li {
        a {
          position: relative;
          display: block;
          font-size:18px;
          font-weight:bold;
          margin:0 20px;
          color: var(--darkTxt);
          &:after {
            display: block;
            content:"";
            position:absolute;
            bottom:-5px;
            left:0;
            width: 0;
            height:2px;
            background: $pointClr;
            transition: all 0.3s;
            opacity: 0;
          }
          &.active, &:hover {
            & {
              color: $pointClr;
            }
            &:after {
              width: 100%;
              opacity: 1;
            }
          }
        }
      }
    }
  }

  .icon {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left:20px;
    button {
      width: 40px;
      height: 40px;
      cursor: pointer;
    }
  }
}
.toggleWrap {
  text-align: center;
  .inputWrap {
    position: relative;
    display: block;
    width: 50px;
    margin-right:10px;
    height: 34px;
    cursor: pointer;
    input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;
    }
    .icoMoon {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      font-size: 34px;
      color: #febc06;
    }
    
    .icoSun {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      font-size: 34px;
      color: #febc06;
    }
  }
}


/* Tablet */
@media only screen and (max-width: 1200px) {
  
}

/* Mobile */
@media only screen and (max-width: 800px) {
  .header {
    padding: 10vw;
    .logo {
      width: 12vw;
      height: 12vw;
    }
  

    .navBtn {
      display: block;
      position: absolute;
      top: 6vw;
      right: 3vw;
      font-size: 6vw;
      color: var(--darkTxt)
    }

    .menuWrap {
      z-index: 5;
      flex-direction: column;
      justify-content: space-between;
      position: fixed;
      top:0;
      right:0;
      width:100vw;
      height:100vh;
      background-color: var(--lightBgClr);
      transition: 0.7s;
      transform: translateX(100vw);
      .menu {
        flex-direction: column;
        height: 85vh;
        li {
          a {
            
            font-size:8vw;
            margin:10vw;
          }
          a.active, &:hover a {
            &:after {
              display: none;
            }
          }
        }
      }

      &.navToggle {
        transform: translateX(0);
      }

      .icon {
        position: absolute;
        bottom:15vw;
        left:0;
        right: 0;
        margin-left:0;
        button {
          width: 15vw;
          height: 15vw;
          cursor: pointer;
          .icoMoon {
            font-size: 15vw;
            color: #febc06;
          }
        }
        button + button {
          margin-left: 10vw;
        }
      }
    }
  }
}