.contactWrap {
  .emailWrap {
    width: 96%;
    max-width: 800px;
    margin: auto;
    padding: 20px;
    border-radius: 10px;
    background:#e7e7e7;
    box-sizing: border-box;
    p {
      color: #333;
      font-size: 14px;
      font-weight: 500;
      text-align: center;
      margin:20px 0;
      .mailLink {
        text-decoration: underline;
        &:hover {
          color:$pointClr;
        }
      }
    }
    strong {
      position: relative;
      display: block;
      width: 310px;
      text-align: center;
      font-size: 22px;
      font-weight: 700;
      margin:20px auto;
      
      .ani {
        position: absolute;
        top:0; 
        right:0;
        animation:ani 1.5s ease infinite;
      }

      @keyframes ani { 
        0%{top:-10%}
        50%{top: 20%}
        100%{top:-10%}
      }
    }
  }
  form {
    margin: auto;
    width: 100%;
    max-width: 600px;
    span {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom:7px;
    }
    label {
      color:#5f5f5f;
      font-size:16px;
      font-weight: 800;

    }
    input[type="text"],
    input[type="email"] {
      width: 500px;
      height:40px;
      border-radius: 7px;
      border: none;
      padding:7px;
      font-size: 15px;
      color:#222;
      font-weight: 500;
      font-family: "Open Sans", sans-serif;
    }
    textarea {
      width: 500px;
      height: 200px;
      border-radius: 7px;
      border: none;
      padding:7px;
      font-size: 15px;
      color:#222;
      resize: none;
      font-weight: 500;
      font-family: "Open Sans", sans-serif;
    }
    input[type="submit"] {
      display: block;
      border:none;
      background: none;
      font-size: 17px;
      font-weight: 700;
      margin:20px auto;
      padding: 10px 30px;
      border-radius: 6px;
      cursor: pointer;
      background: linear-gradient(to left, #FFF 50%, $pointClr 50%) right;
      background-size: 200%;
      transition: all 0.2s ease-in-out;
      &:hover {
        color: #FFF;
        background-position: left;
        border-radius: 20px;
      }
    }
  }
}

/* Mobile */
@media only screen and (max-width: 800px) {
  .contactWrap {
    .emailWrap {
      padding: 3vw;
      border-radius: 4vw;
      p {
        font-size: 4vw;
        text-align: center;
        margin:5vw 0;
      }
      strong {
        width: 82%;
        font-size: 5vw;
        margin:5vw auto;
      }
    }
    form {
      span {
        display: block;
        margin-bottom:5vw;
      }
      label {
        display: block;
        font-size:4vw;
        margin-bottom:1vw;
  
      }
      input[type="text"],
      input[type="email"] {
        width: 100%;
        height:10vw;
        border-radius: 2vw;
        padding:2vw;
        font-size: 4vw;
      }
      textarea {
        width: 100%;
        height: 40vw;
        border-radius: 2vw;
        padding:2vw;
        font-size: 4vw;
      }
      input[type="submit"] {
        font-size: 4.5vw;
        padding: 2.5vw 6vw;
        margin-bottom: 5vw auto;
        border-radius: 2vw;
        background: $pointClr;
        color: #FFF;
      }
    }
  }
}