$pointClr : #ff4747;

.theme-light {
  --darkTxt: #333;
  --lightBgClr:#FFF;
  --darkiconClr: #333;
  --darkBtnClr: $pointClr;
  --logoImg: url(../images/logo.png);
  --circlr: #e2e2e2;
}

.theme-dark {
  --darkTxt: #FFF;
  --lightBgClr:#474747;
  --darkiconClr: #FFF;
  --darkBtnClr:#FFF;
  --logoImg: url(../images/logo_w.png);
  --circlr: #5e5e5e;
}
